@font-face{font-family:Oswald;font-style:normal;font-weight:400;src:local('Oswald Regular'),local('Oswald-Regular'),url(https://fonts.gstatic.com/s/oswald/v11/Y_TKV6o8WovbUd3m_X9aAA.ttf) format('truetype')}body,html{margin:0!important;padding:0!important}.photo-tiles-container{height:100%!important;width:100%!important;position:fixed;background:#000;color:#fff;font-family:Oswald;overflow:hidden}.photo-tiles-container .photo-tiles{-webkit-transition:.5s ease-in-out!important;transition:.5s ease-in-out!important;box-sizing:border-box!important;height:100%!important;width:100%!important;border:3px solid #000}.photo-tiles-container .photo-tiles .photo-tiles-row{-webkit-transition:.5s ease-in-out!important;transition:.5s ease-in-out!important;box-sizing:border-box!important;height:50%;width:100%;position:relative}.photo-tiles-container .photo-tiles .photo-tiles-row .photo{-webkit-transition:.5s ease-in-out!important;transition:.5s ease-in-out!important;box-sizing:border-box!important;background-position:center bottom!important;background-size:cover!important;background-repeat:no-repeat!important;background-color:grey;position:absolute;height:100%;border:3px solid #000;float:left;opacity:0}.photo-tiles-container .photo-tiles .photo-tiles-row .photo.loaded{opacity:1}.photo-tiles-container .photo-tiles .photo-tiles-row .photo.photo-lg{width:40%}.photo-tiles-container .photo-tiles .photo-tiles-row .photo.photo-sm{width:20%}.photo-tiles-container .photo-tiles .photo-tiles-row .photo.photo-sm .photo-sm-dual{height:50%;width:100%;background-position:center bottom!important;background-size:cover!important;background-repeat:no-repeat!important;position:absolute;bottom:0}.photo-tiles-container .photo-tiles .photo-tiles-row .photo.photo-sm .photo-sm-dual:first-child{border-bottom:6px solid #000;top:0;z-index:1}.photo-tiles-container .photo-tiles .photo-tiles-row .photo.remove{opacity:0!important}